import Footer from '@/components/footer/footer'
import Header from '@/components/hearder/Header'
import { createFileRoute, Outlet } from '@tanstack/react-router'
import { useIsFr } from '@/hooks/use-is-fr'
import { NoNetPostComponent } from '@/components/nointernetpost'
import BottomNavbar from '@/components/BottomNavbar'

export const Route = createFileRoute('/news')({
  component: RouteComponent,
  errorComponent: () => {
    const isFr = useIsFr()

    return (
      <>
        <Header bottomBorder />
        <main className="mt-20">
          <NoNetPostComponent isFr={isFr} />
        </main>
        <Footer />
        <BottomNavbar isFr={isFr} />
      </>
    )
  },
})

function RouteComponent() {
  const isFr = useIsFr()

  return (
    <div className="  ">
      <Header bottomBorder />
      <main className="pt-20 lg:pt-0 sm:min-h-screen" key={isFr ? 'fr' : 'ar'}>
        <Outlet />
      </main>
      <BottomNavbar isFr={isFr} />

      <Footer />
    </div>
  )
}
